	background-color: $color-red;
}

body.color-theme-red .blue-cards label.active {
    border: 1px solid $color-red;
}

body.color-theme-red .blue-cards label svg path {
    stroke: $color-red;
}

body.color-theme-red .blue-cards label svg.livrare-la-domiciliu path, body.color-theme-red .blue-cards label svg.ridicare-personala path {
    fill: $color-red;
}

